import { Component } from '@angular/core';
import { HelperService } from './services/helper.service';
import { Contact } from './components/contact-item/contact';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent {
  users!: Contact[] | any;
  filterdUsers!: Contact[] | any;
  constructor(private helper: HelperService) {}
  ngOnInit() {
    this.getUsers();
  }
  getUsers = () => {
    this.helper.getUsers().subscribe((data) => {
      this.users = data;
      this.filterdUsers = data;
    });
  };
  searchValueChange = (event: any) => {
    console.log(event.value);
    let tempList: any[] = [];
    this.users.forEach((element: Contact) => {
      if (
        element.name.toLowerCase().indexOf(event.value.toLowerCase()) !== -1 ||
        element.username.toLowerCase().indexOf(event.value.toLowerCase()) !== -1
      ) {
        tempList.push(element);
      }
    });
    this.filterdUsers = tempList;
  };
}
